import React from 'react'
import Table from './table'
import Form from "./form";

class App extends React.Component{
	state={
		arr:[]
	}
	delete=(index)=>{
		var arr=this.state.arr;
		arr.splice(index,1)
		this.setState({
			arr:arr
		})
	}
	handleSubmit=(data)=>{
		var arr=this.state.arr;
		arr.push(data)
		this.setState({
			arr:arr
		})
  }
	render(){
		return(
			<div>
				<Table list={this.state.arr} delete={this.delete}></Table>
				<Form handleSubmit={this.handleSubmit} />
			</div>
		)
	}
}

export default App;
