import React,{Component} from 'react'

const Theader=()=>{
	return(
		<thead>
			<tr>
				<th>Name</th>
				<th>Job</th>
			</tr>
		</thead>
	)
}
const Tbody=(props)=>{
	var arr=props.list.map((item,index)=>{
		return(
			<tr key={index}>
				<td>{item.name}</td>
				<td>{item.job}</td>
				<td><button onClick={()=>props.delete(index)}>delete</button></td>
			</tr>
		)
	})
	return(
		<tbody>
			{arr}
		</tbody>
	)
}

class Table extends Component{
	render(){
		var newarr=this.props.list;
		return(
			<table>
				<Theader />
				<Tbody list={newarr} delete={this.props.delete} />
			</table>
		)
	}
}

export default Table